import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/main',
  },
  {
    path: '/main',
    name: 'Main',
    component: () => import('../views/Main')
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('../views/Home'),
  },
  {
    path: '/login',
    name: 'login',
    component: ()=>import('../views/Entrance/Login'),
  },
  {
    path: '/register',
    name: 'register',
    component: ()=>import('../views/Entrance/Regis'),
  },
  {
    path: '/findPass',
    name: 'findpass',
    component: ()=>import('../views/Entrance/FindPass'),
  },
  {
    path: '/manage',
    name: 'Manage',
    component: () => import('../views/Manage'),
  },
  {
    path: '/personal',
    name: 'PersonalCenter',
    component: () => import('../views/PersonalCenter'),
  },
  {
    path: '/questionnare',
    name: 'Questionnare',
    component: () => import('../views/Questionnare'),
  },
  {
    path: '/surveyquestionnare/:id',
    name: 'surveyquestionnare',
    component: ()=>import('../views/SurveyQuestionnare'),
  },
  {
    path: '/vote/questionnare/:id',
    name: 'votequestionnare',
    component:()=>import('../views/Vote/VoteQuestionnare'),
  },
  {
    path: '/exam/questionnare/:id',
    name: 'examquestionnare',
    component:()=>import('../views/Exam/ExamQuestionnare'),
  },
  {
    path: '/signup/questionnare/:id',
    name: 'signupquestionnare',
    component:()=>import('../views/SignUp/SignUpQuestionnare'),
  },
  {
    path: '/preview',
    name: 'Preview',
    component: () => import('../views/Preview/Preview'),
  },
  {
    path: '/standardpreview',
    name: 'StandardPreview',
    component: () => import('../views/Preview/StandardPreview')
  },
  {
    path: '/clockinpreview',
    name: 'ClockInPreview',
    component: () => import('../views/Preview/ClockInPreview')
  },
  {
    path: '/clockinquestionnare/:id',
    name: 'ClockInQuestionnare',
    component: () => import('../views/ClockInQuestionnare')
  },
  {
    path: '/answer/:id',
    name: 'Answer',
    component: () => import('../views/Answer/Answer'),
  },
  {
    path: '/exam/result/:id',
    name: 'ExamAnswer',
    component: () => import('../views/Answer/ExamAnswer'),
  },
  {
    path: '/datapreview',
    name: 'DataPreview',
    component: () => import('../views/Preview/DataPreview')
  },
  {
    path: '/result',
    name: 'Result',
    component: () => import('../views/Answer/Result')
  },
  {
    path: '/endresult',
    name: 'Result',
    component: () => import('../views/Answer/EndResult')
  },
  {
    path: '/begin',
    name: 'Begin',
    component: () => import('../views/Answer/Begin')
  },
  {
    path: '/pdf',
    name: 'Pdf',
    component: () => import('../views/Preview/pdf'),
  },
  {
    path: '/datapdf',
    name: 'DataPDF',
    component: () => import('../views/Preview/DataPDF'),
  }
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
