import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import vuetify from "./plugins/vuetify";
import axios from "axios";
import Message from './utils/notification/index.js'
import QuestionList from "@/assets/Globalvariables/QuestionList";
import Temp from "@/assets/Globalvariables/Temp";
import Qsnum from "@/assets/Globalvariables/Qsnum";
import {VueJsonp} from 'vue-jsonp'
import '@mdi/font/css/materialdesignicons.css'

Vue.prototype.addQuestions = function(index, list, q){
  list.splice(index, 1, q);
}
Vue.prototype.QuestionList = QuestionList;
Vue.prototype.Temp = Temp;
Vue.prototype.s = Qsnum;
Vue.prototype.$message = Message;
Vue.config.productionTip = false;
axios.defaults.baseURL = 'http://101.35.86.15:8080/'
axios.defaults.timeout=6000
Vue.prototype.$axios = axios;
Vue.use(VueJsonp);
axios.interceptors.request.use((config) => {
  // 判断是否存在token，如果存在的话，则每个http header都加上token
  let token = localStorage.getItem('Authorization')
  if (token) {
      config.headers.Authorization = token;
  }
  console.log('config',config);
  return config;
},
error => {
  return Promise.reject(error);
});



new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
