import Vue from 'vue'
import Vuex from 'vuex'
import notification from "./module/notification.js";
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    login: false,
    user_id: -1,
    question:[],
    router: '',
    UserName:'',
    // manage begin
    selectedPlace: 0,
    // manage end
  },
  mutations: {
    initUserId(state,user_id) {
      state.user_id=user_id
    },
    saveQuestion(state, payload) {
      state.question=payload
    }
  },
  actions: {
  },
  modules: { message: notification }
})
