<template>
  <div style="height:60px;background-color:white;">
    <div style="height:60px;">
      <img src="@/assets/app/logo.png" alt="" class="headLogoClass">
      <span class="headCharClass" style="font-family:'bgtitlefamily'">问卷系统</span>
      <!-- 路由begin -->
      <div style="float:right;margin-top: 5px;" v-if="this.$route.path!='/login'&&this.$route.path!='/register'&&this.$route.path!='/findpass'">
        <div style="display: inline-block;">
          <v-tabs v-model="$store.state.router">
            <v-tab><router-link class="routerClass" to="/home">首&nbsp;&nbsp;页</router-link></v-tab>
            <v-tab><router-link class="routerClass" to="/manage">问卷管理</router-link></v-tab>
            <v-tab><router-link class="routerClass" to="/personal">个人中心</router-link></v-tab>
          </v-tabs>
        </div>

        <div class="loginClass">
          <div style="height:35px;width:35px;margin-left:4px;margin-top:3px;border-radius:22px;overflow:hidden;display:inline-block;background-color:white;">
            <img :src="path" alt="" style="height:45px;display:inline-block;">
          </div>
          <div class="loginFontClass">{{UserName}}</div>
          <div style="float:right;position:relative;top:6px;right:15px;">
            <v-menu
              transition="scale-transition"
              origin="center center"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  dark height="20px" style="width:20px;"
                  color="transparent"
                  v-bind="attrs"
                  v-on="on"
                  fab
                >
                  <img src="@/assets/app/triangle.png" alt="" style="height:15px;position:relative;">
                </v-btn>
              </template>
              <v-btn width="100px" style="background-color:skyblue;color:white;" @click="logout">
                <img src="@/assets/app/logout.png" alt="" style="width:20px;">&nbsp;退出登录
              </v-btn>
            </v-menu>
          </div>
        </div>


      </div>
      <!-- 路由end -->
    </div>
    <v-divider></v-divider>
  </div>
</template>

<script>
import "@/assets/font/font.css";
export default {
  name: 'Header',
  methods: {
    logout() {
      this.$router.replace('/login');
    },
  },
  mounted() {
    this.$axios
      .post("/personalcenter1", {
        UserId: localStorage.getItem("id"),
      })
      .then((response) => {
        this.UserName = response.data.UserName;
        this.path = '101.35.86.15:8080/image/'
        this.path += response.data.path.slice(13);
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  data() {
    return {
      UserName: '',
      path: '',
    }
  }
};
</script>
<style >
.routerClass {
  text-decoration: none;
  display: inline-block;
  height: 48px;
  line-height: 48px;
  color: rgb(102, 102, 102);
}

.loginBtnClass {
  position: relative;
  top: -20px;
  margin: 0 5px;
}

.headLogoClass {
  width: 45px;
  margin-top: 10px ;
  margin-left: 30px;
}

.headCharClass {
  display: inline-block;
  position: relative;
  font-family:'bgtitlefamily';
  text-shadow: 1px 1px 1px rgba(0, 0, 0, .3);
  font-size: 24px;
  margin-left: 30px;
  height: 60px;
  line-height: 60px;
  top: -15px;
}

.loginClass {
  width:200px;
  height:40px;
  display:inline-block;
  position:relative;
  border-radius: 20px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .3);
  margin-right: 10px;
  margin-left: 10px;
  top: -5px;
}

.loginFontClass {
  display:inline-block;
  width:100px;
  position:relative;
  top:-7px;
  margin-left:10px;
  overflow:hidden;
  white-space:nowrap;
  text-overflow:ellipsis;
}
</style>

